"use client";

import React, { useState } from 'react';
import { useRouter } from 'next/navigation';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';
import { Alert, AlertDescription } from '@/components/ui/alert';

export default function LoginPage() {
  const [ra, setRa] = useState('');
  const [password, setPassword] = useState('');
  const [isLoading, setIsLoading] = useState(false);
  const [error, setError] = useState('');
  const router = useRouter();

  // Credenciais de exemplo para o protótipo
  const validCredentials = {
    ra: '202123456',
    password: 'senha123'
  };

  const handleLogin = async (e: React.FormEvent) => {
    e.preventDefault();
    setIsLoading(true);
    setError('');

    // Simular delay de autenticação
    await new Promise(resolve => setTimeout(resolve, 1500));

    if (ra === validCredentials.ra && password === validCredentials.password) {
      // Login bem-sucedido - salvar no localStorage para manter sessão
      localStorage.setItem('isAuthenticated', 'true');
      localStorage.setItem('studentRA', ra);
      router.push('/dashboard');
    } else {
      setError('RA ou senha incorretos. Tente novamente.');
    }

    setIsLoading(false);
  };

  return (
    <div className="min-h-screen bg-gradient-to-br from-blue-100 via-white to-indigo-100 flex flex-col justify-center p-4">
      {/* Header */}
      <div className="text-center mb-8">
        <div className="mb-4">
          <img
            src="https://placehold.co/120x120?text=UFB+Logo"
            alt="Logo da universidade em design moderno"
            className="w-20 h-20 mx-auto rounded-full border-4 border-blue-200 shadow-lg"
            onError={(e) => {
              e.currentTarget.src = "https://placehold.co/120x120?text=UFB+Logo";
            }}
          />
        </div>
        <h1 className="text-2xl font-bold text-gray-800 mb-2">
          Carteirinha Digital
        </h1>
        <p className="text-gray-600 text-sm">
          Universidade Federal do Brasil
        </p>
      </div>

      {/* Login Form */}
      <div className="max-w-md mx-auto w-full">
        <Card className="shadow-xl border-0">
          <CardHeader className="text-center pb-4">
            <CardTitle className="text-xl text-gray-800">Acesso do Estudante</CardTitle>
            <CardDescription className="text-gray-600">
              Entre com seu RA e senha para acessar sua carteirinha digital
            </CardDescription>
          </CardHeader>
          <CardContent>
            <form onSubmit={handleLogin} className="space-y-4">
              {error && (
                <Alert className="border-red-200 bg-red-50">
                  <AlertDescription className="text-red-700">
                    {error}
                  </AlertDescription>
                </Alert>
              )}

              <div className="space-y-2">
                <Label htmlFor="ra" className="text-sm font-medium text-gray-700">
                  RA (Registro Acadêmico)
                </Label>
                <Input
                  id="ra"
                  type="text"
                  placeholder="Digite seu RA"
                  value={ra}
                  onChange={(e) => setRa(e.target.value)}
                  required
                  className="h-12 text-base"
                  disabled={isLoading}
                />
              </div>

              <div className="space-y-2">
                <Label htmlFor="password" className="text-sm font-medium text-gray-700">
                  Senha
                </Label>
                <Input
                  id="password"
                  type="password"
                  placeholder="Digite sua senha"
                  value={password}
                  onChange={(e) => setPassword(e.target.value)}
                  required
                  className="h-12 text-base"
                  disabled={isLoading}
                />
              </div>

              <Button 
                type="submit" 
                className="w-full h-12 bg-blue-600 hover:bg-blue-700 text-base font-medium"
                disabled={isLoading}
              >
                {isLoading ? (
                  <div className="flex items-center space-x-2">
                    <div className="w-4 h-4 border-2 border-white border-t-transparent rounded-full animate-spin"></div>
                    <span>Entrando...</span>
                  </div>
                ) : (
                  'Entrar'
                )}
              </Button>
            </form>

            {/* Demo Credentials */}
            <div className="mt-6 p-4 bg-blue-50 rounded-lg border border-blue-200">
              <h4 className="text-sm font-medium text-blue-800 mb-2">
                📋 Credenciais para Demonstração:
              </h4>
              <div className="text-xs text-blue-700 space-y-1">
                <p><strong>RA:</strong> 202123456</p>
                <p><strong>Senha:</strong> senha123</p>
              </div>
            </div>
          </CardContent>
        </Card>

        {/* Footer Links */}
        <div className="mt-6 text-center space-y-2">
          <Button variant="link" className="text-blue-600 text-sm" disabled>
            Esqueci minha senha
          </Button>
          <br />
          <Button variant="link" className="text-blue-600 text-sm" disabled>
            Primeiro acesso? Cadastre-se
          </Button>
        </div>
      </div>

      {/* Footer */}
      <footer className="text-center mt-8 py-4">
        <p className="text-xs text-gray-500">
          © 2024 Universidade Federal do Brasil - Sistema Acadêmico
        </p>
      </footer>
    </div>
  );
}
